@import url(https://fonts.googleapis.com/css2?family=Sora:wght@100;200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700;800;900&family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap);
*{
  outline: none;
  /* -webkit-tap-highlight-color: transparent;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; */
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
svg {
  height: 1rem;
}
html{
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  width: 0px;
}
body {
  --cbl: #1FB4FA;
  --cbd: #2F85AD;

  --crl: #FA5066;
  --crd: #AD2638;

  --cyl: #FAFFE8;
  --cyd: #FAF469;

  --cgl: #11d48a;
  --cgn: #01a365;
  --cgd: #087249;

  --col: #E87D0E;
  --con: #E47500;
  --cod: #c96b00;

  --cbd: #411010;

  --fsss: 'Sora', sans-serif;
  --fssp: 'Poiret One', sans-serif;
  --fsc: 'Cinzel', serif;
  --fsr: 'Rubik', sans-serif;
  --fsd: 'Dancing Script', cursive;

  --xxsf: clamp(0.6rem, 2vw, 0.8rem);
  --xsf: clamp(0.8rem, 2vw, 1rem);
  --sf: clamp(1rem, 2vw, 1.2rem);
  --mf: clamp(1.3rem, 4vw, 1.8rem);
  --bf: clamp(2.5rem, 6vw, 4rem);
  --xbf: clamp(3.5rem, 6vw, 5rem);
  

  font-family: var(--fsr);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

